import uniSearchBar from '@/components/uni-search-bar/uni-search-bar.vue';
import cate from './children/cate/cate.vue';
import floor from './children/floor/floor.vue';

export default {
	data() {
		return {
			//轮播图数组
			swiperList: [],
			// 导航数组
			cateList: [],
			// 楼层数据
			floorList: []
		};
	},
	components: {
		uniSearchBar,
		cate,
		floor
	},
	onLoad() {
		// 发送异步请求，获取轮播数据
		this.getswiperList();
		this.getCateList();
		this.getFloorList();
	},
	methods: {
		search() {
			uni.navigateTo({
				url: '../search/search'
			});
		},
		// 获取轮播数据
		async getswiperList() {
			const res = await this.$http({
				url: '/home/swiperdata'
			});
			this.swiperList = res.data.message;
		},
		// 获取导航数据
		async getCateList() {
			const res = await this.$http({
				url: '/home/catitems'
			});
			this.cateList = res.data.message;
		},
		// 获取楼层数据
		async getFloorList() {
			const res = await this.$http({
				url: '/home/floordata'
			});
			this.floorList = res.data.message;
		}
	}
};
